import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { CardActionArea } from "@mui/material";
import TypographyMetadata, {
  TYPOGRAPHY_METADATA_KIND,
} from "./typography/TypographyMetadata";
import { PixyNavLink } from "./PixyNavLink";
import { namedRoutes } from "../../Routes";

export interface PixyNFTCardProps {
  campaignSlug: string;
  videoSlug: string;
  image: string;
  title: string;
  ustPrice: number;
  isVariablePrice?: boolean;
  shortDescription: string;
  description: string;
}

export const PixyVideoCard = ({
  campaignSlug,
  videoSlug,
  image,
  title,
  ustPrice,
  isVariablePrice,
  shortDescription,
  description
}: PixyNFTCardProps) => {
  return (
    <Card
      sx={{
        boxShadow:
          "10px 10px 30px rgba(0, 0, 0, 0.25), -5px -5px 15px rgba(255, 255," +
          " 255, 0.04)",
        background:
          "linear-gradient(101.73deg, rgba(255, 255, 255, 0.05)" +
          " -6.48%, rgba(0, 0, 0, 0.05) 106.74%), rgba(255, 255, 255, 0.02)",
        borderRadius: "0px",
        border: "1px solid rgba(255, 255, 255, 0.04)",
      }}
    >
     <CardActionArea disableRipple>
        <PixyNavLink
          to={namedRoutes.campaignVideoPath()}
          sx={{width: "100%",}}
        >
          <img src={image} style={{width: "100%"}}></img>
          <CardContent sx={{backgroundColor: "black"}}>
            <TypographyMetadata
              center
              gutterBottom
              kind={TYPOGRAPHY_METADATA_KIND.NFTTitle}
              sx={{
                display: "block"
              }}
            >
              {title}
            </TypographyMetadata>
            <Typography
              sx={{
                fontSize: "21px",
                fontWeight: 400,
                lineHeight: "30px",
                letterSpacing: "0px",
                textAlign: "left"
              }}
            >
              {description}
            </Typography>
          </CardContent>
        </PixyNavLink>
     </CardActionArea>
    </Card>
  );
};
